.close-button {
  position: absolute;
  top: 0.5em;
  right: 0.5em;
  font-size: 2em;
  color: #3b3b3b;
}

.close-button:hover {
  color: #808080;
}

.close-button:active {
  transform: translateY(1px);
  will-change: transform;
}

.close-button .close-icon {
  text-align: center;
}